
*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}


.main-section
{
    background-color: whitesmoke;
    height: auto;
}

.productsContainer
{
    position: relative;
    width: 95vw;
    margin: 0px auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    text-align: center;
    grid-gap: 50px;
    overflow: hidden;
    padding:120px 30px 30px;
   
}

.single
{
    /* width: 100%;
    height: 100%; */
}

.singleContainer
{
  
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.4);
    transition: 1s all;
    overflow: hidden;
    padding-top: 20px;
}

.singleContainer:hover
{
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.4);
}

.img-container 
{
    transition: 1s all;
    position: relative;  
   
}

.img-container:hover .imgFluid
{
        transform: scale(1.1);
        transition: 1s all;
}

.imgFluid

{
    width: 100%;
    height: 100%;
    padding-bottom: 10px;
   
}

.btn-Primary
{
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: transparent;
    border: none;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    background-color: blue;
    color: white;
    padding: 4px 8px;
    border-top-left-radius: 15px;
    transform: translateX(100%);
    cursor: pointer;
}

.btn-Primary:hover{
    background-color: rgb(18, 18, 139);
    color: black;
}

.singleContainer:hover .btn-Primary
{
    transform: translateX(0%);
    transition: 1s all;
}

.titlePrice
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: antiquewhite;
    font-family: sans-serif;
    padding: 5px;
}

.titlePrice h2
{
        font-size: 1rem;
}

.titlePrice h3
{
        font-size: 1.3rem;
}





.detailSection
{
  
    overflow: hidden;
    width: 90vw;
    padding-top: 120px;
}

.detailIn
{
    display: grid;
    grid-template-columns: 1fr 2fr;
    place-items: start;
    min-height: 100vh;
    background-color: rgb(247, 247, 247);
    padding: 50px;
    font-family: sans-serif;
}




.detailDESC h1, h2,h3,h4
{
   margin: 10px 0;
   color: rgba(0,0,0,0.7);
}

.detailDESC h3
{
    font-family: 'Kalam', cursive;
}



.detailDESC p
{
    color: rgba(0,0,0,0.5);
    letter-spacing: 2px;
    line-height: 2;
    text-transform: capitalize;
    font-weight: 600;
    
}


.btnPrimary,.btnPrimar
{
   
    background-color: transparent;
    border:1px solid blue;
    text-transform: capitalize;
    font-family: sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    color: blue;
    padding: 8px;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 10px;
  
}

.btnPrimar
{
    color: orange;
    border:1px solid orange;
}


@media screen and (max-width: 945px)
{

    .detailSection
    {
        overflow: hidden;
        width: 98vw;
        padding-top: 120px;
    }

    .detailIn
    {
        grid-template-columns: 1fr;
    }

    .detailDESC h1, h2,h3,h4
    {
       margin: 5px 0;
       font-size: 1.3rem;
    }


    .detailDESC p
    {
        letter-spacing: 1px;
        line-height: 1.5;
        font-weight: 400; 
    }

   
}

@media screen and (max-width: 945px)
{
    .detailDESC h1, h2,h3,h4
    {
       margin: 5px 0;
       font-size: 1.1rem;
    }

    .detailDESC p
    {
        line-height: 1.3;
        font-weight: 400; 
    }
}


.overlay
{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
}

.overlaycontent
{
    position: relative;
    max-width: 400px;
    background-color: white;
    top: 54%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    padding: 40px 30px 20px;
    border-radius: 10px;
    box-shadow: 0 0 5px 1px rgba(0,0,0,0.4);
    font-family: sans-serif;
    
}

.overlaycontent p
{
font-size: 1.5rem;
margin-bottom: 10px;
letter-spacing: 2px;
color: rgba(0,0,0,0.9);
text-transform: capitalize;
font-weight: 700;
}

.overlaycontent h3
{
font-size: 1.5rem;
letter-spacing: 3px;
color: rgba(0,0,0,0.5);
text-transform: uppercase;
}



.openModal
{
    display: block;
}

.closeModal
{
    display: none;
}

.mainCart
{
    text-align: center;
    padding-top: 50px;
    min-height: 100vh;
   
}


.cartColumns
{
  
    text-transform: uppercase;
    font-family: 'Kalam', cursive;
    font-size: 1.2rem;
   
}


.cartColumnsdata,.cartColumns
{
    display: grid;
    grid-template-columns: repeat(6,1fr);
    text-align: center;
    margin: 0 auto;
    margin: 50px;
  
    

}

.cartColumnsdata{
    font-family: sans-serif;
    font-weight: 600;
}




.totalPart h1
{
    font-family: 'Kalam', cursive;
    letter-spacing: 2px;
}

.quantityComp
{
    display: flex;
    justify-content: center;
  
}

.quantityComp p
{
    position: relative;
    margin: 2px;
    border: 1px solid black;
    width: 40px;
    height: 40px;
    text-align: center;
    transition: 1s all;
    cursor: pointer;
    

  
    
}
.quantityComp p:hover{
    background-color: black;
    color: white;
}

.quantityComp span
{
    position: absolute;
    top: 50%;
    right: 50%;
   
    transform: translate(50%, -50%);
}

.fa-trash
{
    color: orange;
}

@media screen and (max-width: 700px)
{
    .navTitle
    {
        font-size: 1.5rem!important;
        letter-spacing: 10px!important;
    }

}









